
.welcomeMsg{
    display: flex;
    background-color: #2B2828;   
    color: white;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    font-size: 200%;
    position: absolute;
    width: 400px;
    height: 200px;
    border-radius: 10px;
    border: 3px solid rgb(140, 102, 245);
}
.welcomeMsg button{
    cursor: pointer;
    font-size: 20px ;
    padding: 10px 20px;
    letter-spacing: 1px;
    background-color:  rgb(113, 62, 255);
    color:white;
    border: none;
    border-radius: 10px;
    margin: 10px;
}
.logout1{
    font-size: 13px  !important;
    padding: 5px 10px  !important;
    background-color: transparent !important;
    border: 4px solid rgb(113, 62, 255) !important;
}

.welcomeMsg button:hover{
background-color: rgb(117,17,255);
}

@media (max-width:480px) {
    .welcomeMsg{
        width: 80vw;
    }
}